import React, { useEffect, useState } from 'react';
import Labelwithdescription from '../../Components/labelwithdescription/Labelwithdescription';
import { GET_MONITOR_COUNT, GET_MONITOR_VIEWS } from '../../API_Requests';

export default function MonitoringStation() {
    const [monitorCount, setMonitorCount] = useState(0);  // Track monitor count, update once


    // Assuming count is a number

    useEffect(() => {
        // Initial fetch for monitor count
        GET_MONITOR_COUNT()
            .then(count => {
                setMonitorCount(count); // Set the monitor count from the API
            })
            .catch(err => {
                console.error("Error fetching monitor count:", err);
            });

        let isMounted = true;

        const fetchMonitorViews = async () => {
            try {
                const response = await GET_MONITOR_VIEWS();
                // console.log(response)// Wait for the response
                if (response.status === 200) { // Only proceed on success
                    if (isMounted) {
                        setTimeout(fetchMonitorViews, 100); // Wait 5 seconds
                    }
                } else {
                    console.warn(`GET_MONITOR_VIEWS failed with status: ${response.status}`);
                    // No setTimeout here - stops polling until manually restarted
                }
            } catch (error) {
                console.error("Error fetching monitor views:", error);
                // No setTimeout here - stops on error
            }
        };

        fetchMonitorViews(); // Start the loop

        // Cleanup function
        return () => {
            isMounted = false; // Cleanup flag
            console.log("Component unmounted, polling stopped!");
        };
    }, []); // Empty dependency array: runs once on mount



    return (
        <>
            <div className='py-5'>
                <Labelwithdescription
                    label="Monitoring Station"
                    description="Currently watching spaces by this CPGS system (Maximum Capacity is 3 spaces)"
                />
                <p className='text-4xl font-bold'>{monitorCount}</p>
            </div>
            <hr className='my-5' />
            <div>
                <Labelwithdescription
                    label="Live Monitor"
                    description="Here you can monitor your spaces that are currently being monitored."
                />
            </div>
            <div className='flex' id='spaceViewContainer'>
                {/* Space for live monitoring UI */}
            </div>
            <button className='text-white bg-blue-500 rounded-md px-3 py-1' onClick={GET_MONITOR_VIEWS}>Get Next Shot</button>

            <hr className='my-5' />
            <Labelwithdescription
                label="Detection Value"
                description="Once the Object Sensing Value crosses this line, the space is marked as occupied."
            />
            <p className='text-4xl font-bold'>50</p>
        </>
    );
}
