// Required imports
import axios from "axios";
import Cookies from "js-cookie";

// Global Varialbles
let cpgsServerIp = ""
let header = {
  'Content-Type': 'application/json',
  'X-CSRFToken': getCookie('csrftoken'), // Include CSRF token in header
}
// let cpgsServerIp = "http://localhost:8000" 



// VALIDATING FUNCTIONS
const CheckUser = () => {
  let token = Cookies.get('token')
  if (!token) {
    console.log('relogin')
    window.location.href = "/#/login"
  }
  return token
}


// REBOOT PI
export const REBOOT_SYSTEM = async()=>{
  let networksettings = await GETNETWORKSETTINGS()
  axios.get(`${cpgsServerIp}/reboot`)
  window.location.href = `http://${networksettings.host_name}.local:8000`
}


// GET COOKIES
function getCookie(name) {
  let cookieValue = null;
  if (document.cookie && document.cookie !== '') {
    const cookies = document.cookie.split(';');
    for (let i = 0; i < cookies.length; i++) {
      const cookie = cookies[i].trim();
      if (cookie.substring(0, name.length + 1) === (name + '=')) {
        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
        break;
      }
    }
  }
  return cookieValue;
}



// HANDLES LOGIN FUNCTRION
export const LOGIN_API = async (username, password) => {
  let data = {
    'username': username,
    'password': password
  }
  let res
  await axios.post(`${cpgsServerIp}/account_handler`, data, {
    headers:header
  }).then((resdata) => {
    res = resdata
  }).catch((error) => {
    res = error
  })
  return res
}



